import React, {Component} from 'react';
import '../css/pages/home.css';

import {isMobile} from 'react-device-detect';

import Typing from 'react-typing-animation';

class Home extends Component{
    constructor(props){
        super(props)
        this.state = {
            ul_in: '',
            a_show: '',
        }
    }


    classUpdate=()=>{
        
        // alert('yes');
       if(!isMobile){
        this.setState({
            ul_in: 'ul-in',
            a_show: 'a-show',
        });
       }
    }


    componentDidMount(){
        if(isMobile){
            this.setState({
                ul_in: 'ul-in',
                a_show: 'a-show',
            });
        }
    }
    render(){
        return(
            <div className='Home'>
                <div className='left'>

                <div className='space-div-up'>
                    
                </div>
                    <Typing>
                    <h4>I am <br></br>Nelson Bassey</h4>
                    </Typing>

                    <Typing 
                        startDelay = {1500}
                        speed = {10}
                    >
                    <p>Computer Science Student at African Leadership <br></br> University</p>
                    </Typing>

                    <div className='space-div'>
                    
                    </div>

                    <div className='social_icons_div'>
                        <a target="_blank" href="https://github.com/nellybaz" >
                            <img src={require('../images/github-logo.png')} />
                        </a>
                        <a target="_blank" href="https://www.linkedin.com/in/nbassey/" >
                            <img src={require('../images/linkedin-logo.png')} />
                        </a>

                        <a className={`a ${this.state.a_show}`} href={require('../images/NelsonBassey_Resume.pdf')} download>DOWNLOAD MY RESUME</a>
                    </div>
                </div>

                <div className='right'>

                    <div className='experience'>
                    <Typing 
                        startDelay = {3500}
                        speed = {1}
                        onFinishedTyping = {()=> this.classUpdate()}
                    >
                        <h4>
                            Programming Languages
                        </h4>
                    </Typing>


                    <ul className={`${this.state.ul_in}`}>
                        <li>
                            Java (Favourite)
                        </li>

                        <li>
                            Python (First Thought)
                        </li>

                        <li>
                            Javascript 
                        </li>

                        <li>
                            Dart 
                        </li>

                        <li>
                            PHP 
                        </li>
                    </ul>

                    {/* <p>Talent is never a match for</p> */}
                   

                    </div>


                    <div className='experience'>
                    <Typing 
                        startDelay = {3500}
                        speed = {1}
                        // onFinishedTyping = {()=>alert('finished')}
                    >
                        <h4>
                            Frameworks and Tools
                        </h4>
                    </Typing>


                    <ul className={`${this.state.ul_in}`}>
                        <li>
                            ReactJs / ReactNative
                        </li>

                        <li>
                            Flutter
                        </li>

                        <li>
                            Figma 
                        </li>

                        <li>
                            Git 
                        </li>

                    </ul>

                    </div>
                
                </div>

            </div>
        )
    }
}

export default Home;